import React from 'react'
import { BriefHeader as Header, BriefTitle } from './style'

interface Props {
  title?: string | null
  highlight?: number
  subTitle?: string
  children?: React.ReactNode
  index?: boolean
}

export const BriefHeader = ({ highlight, subTitle, title, children, index }: Props) => {
  return (
    <Header className={index ? 'index' : ''}>
      {/* {subTitle && (
        <BriefSubTitle>
          {highlight && <PurpleTitle>{highlight}</PurpleTitle>}
          {subTitle}
        </BriefSubTitle>
      )} */}
      {title && <BriefTitle>{title}</BriefTitle>}
      {children && children}
    </Header>
  )
}
